import React from "react";
import "./Projects.css";
import StandardPage from "../components/StandardPage";
import ProjectCard from "../components/ProjectCard";
import { Box } from "@mui/system";

export default function Projects() {
  return (
    <StandardPage>
      <h1>Projects</h1>
      <Box
        sx={{
          width: "98%",
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          flexWrap: "wrap",
          gap: "1vmin",
        }}
      >

        <ProjectCard
          title="URCA Project"
          src="data:image/png;base64,"
          url="https://github.com/MattaURCA-S22/MicrAgg-WebApp"
        >
          Part of my current independent study!
        </ProjectCard>

        <ProjectCard
          title="Crowgenium"
          src="https://crowgenium.weebly.com/uploads/1/4/0/0/140099879/crowgeniumwhite_orig.png"
          url="https://crowgenium.weebly.com"
        >
          HCI Design Project
        </ProjectCard>


        <ProjectCard
          title="Portfolio"
          src="data:image/png;base64,"
          url="https://github.com/majornoid/majornoid.github.io"
        >
          You're looking at it! (Click for the repo)
        </ProjectCard>

      </Box>
    </StandardPage>
  );
}
